
body{
    margin: 0 0 0 0;
}
.header{
    margin: 0%;
    padding: 0;
    width: 100%;
    height: 10vh;
    min-height: 100px;

    background-color: #181818;
    color:#f5a018;

    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f5a018;
}
.header h1{
    width: 30%;
    text-align: center;
}
.header nav {
    height: 100%;
    width: 45%;

    display: flex;
    justify-content: space-around;
    align-items: center;

    font-size: 20px;
}
.header nav button{
    color: #E5E7E9;
    background: #181818;
    border: 1px solid #E5E7E9;
    border-radius: 5px;
    padding: 10px 20px;
}
.header nav button:hover{
    color: #181818;
    background: #E5E7E9;
    border: 1px solid #E5E7E9;
}
.main-component{
    height: 75vh;
    margin: 0%;
    padding: 0%;
    background: #FDFEFE ;
}
.footer{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 10vh;
    min-height: 100px;
    background-color: #181818;
    color: #f5a018;
    border-top: 1px solid #f5a018;
}
.footer h2{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer p {
  width: 100%;
  height: 100%;
  justify-content: center;
  justify-items: center;
  vertical-align: middle;
  text-align: center;
}

#User{
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}
#User h2{
    font-size: 50px;
    color: #283747;
}
#User span{
    color: crimson;
    font-weight: bold;
}

#UserBalance{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#UserBalance h2{
    font-size: 50px;
    color: #283747;
}
#UserBalance span{
    color: crimson;
    font-weight: bold;
}

#UserBalance {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#UserBalance h2 {
    font-size: 50px;
    color: #283747;
}
#UserBalance span {
    color: crimson;
    font-weight: bold;
}
button{
    color: #000000;
    background: #f5a018;
    border: 1px solid #007efc;
    border-radius: 5px;
    padding: 10px 20px;
}
button:hover{
    color: #000000;
    background: #7fbfff;
    border: 1px solid #358035;
}
